a{border-radius:8px}.sticky-top{border-bottom:3px solid black;background:#191919;justify-content:center}.navbar-light{font-family:'Montserrat', sans-serif;text-align:center}button.menuToggle{justify-content:center;color:white;padding-top:10px;padding-bottom:10px;border-color:transparent;border-style:hidden}a.title{position:absolute;top:-225px;color:white;background-color:black;font-size:2rem;font-weight:900;opacity:0.8;box-shadow:0 0 16px #000, 0 0 32px #000;-webkit-animation:border-flicker 2s linear forwards, letter-flicker 3s linear infinite;animation:border-flicker 2s linear forwards, letter-flicker 3s linear infinite;mix-blend-mode:normal}@media (min-width: 900px){a.title{font-size:3rem;top:-250px}}.navbar-light .navbar-toggler{color:white}.navbar-light .navbar-nav .nav-link{border:3px solid transparent;color:#ffffff;background-color:transparent;padding:.5em 1.5em .5em}@media only screen and (min-width: 800px){a.nav-link:hover{-webkit-animation:border-flicker 5s linear forwards, letter-flicker 8s linear infinite;animation:border-flicker 5s linear forwards, letter-flicker 8s linear infinite}}@media only screen and (min-width: 800px){a.nav-link.active{-webkit-animation:border-flicker 2s linear forwards, letter-flicker 3s linear infinite;animation:border-flicker 2s linear forwards, letter-flicker 3s linear infinite}}@-webkit-keyframes letter-flicker{2%,6%,11%,18%,32%,37%,40%,100%{color:orangered;text-shadow:0 0 50px orangered}3%,9%,14%,33%,39%{color:darkred;text-shadow:none}}@keyframes letter-flicker{2%,6%,11%,18%,32%,37%,40%,100%{color:orangered;text-shadow:0 0 50px orangered}3%,9%,14%,33%,39%{color:darkred;text-shadow:none}}@-webkit-keyframes border-flicker{2%,5%,7%,13%,18%,34%,54%,100%{border:3px solid cornflowerblue;box-shadow:0 0 50px -1px cornflowerblue, 0 0 12px -1px cornflowerblue inset}3%,6%,9%,16%,22%,36%{border:3px solid midnightblue;box-shadow:none}}@keyframes border-flicker{2%,5%,7%,13%,18%,34%,54%,100%{border:3px solid cornflowerblue;box-shadow:0 0 50px -1px cornflowerblue, 0 0 12px -1px cornflowerblue inset}3%,6%,9%,16%,22%,36%{border:3px solid midnightblue;box-shadow:none}}

